import { ACLService } from './../acl/acl.service';
import { Injectable } from '@angular/core';
import { ACLType } from '../acl/acl.type';

export interface Menu {
    /**  */
    text: string;
    /** i18n */
    translate?: string;
    /**  */
    group?: boolean;
    /** angular  */
    link?: string;
    /**  */
    externalLink?: string;
    /**  target */
    target?: '_blank' | '_self' | '_parent' | '_top';
    /**  */
    icon?: string;
    /** `group:true`  */
    badge?: number;
    /**  */
    badge_dot?: boolean;
    /**  Badge  error https://github.com/cipchk/ng-alain/blob/master/_documents/utils.md# */
    badge_status?: string;
    /**  */
    hide?: boolean;
    /** ACL */
    acl?: string | string[] | ACLType;
    /**  */
    children?: Menu[];
    /**
     *  Service 
     * 1
     * 2
     * 3
     */
    _type?: number;
    /**  */
    _selected?: boolean;
    /**  */
    _open?: boolean;
    _depth?: number;

    [key: string]: any;
}

@Injectable()
export class MenuService {

    private data: Menu[] = [];

    constructor(private aclService: ACLService) { }

    visit(callback: (item: Menu, parentMenum: Menu, depth?: number) => void) {
        const inFn = (list: Menu[], parentMenu: Menu, depth: number) => {
            for (const item of list) {
                callback(item, parentMenu, depth);
                if (item.children && item.children.length > 0) {
                    inFn(item.children, item, depth + 1);
                } else {
                    item.children = [];
                }
            }
        };

        inFn(this.data, null, 0);
    }

    add(items: Menu[]) {
        this.data.push(...items);
        this.resume();
    }

    /**
     * 
     */
    resume() {
        let i = 1;
        this.visit((item, parent, depth) => {
            item.__id = i++;
            item.__parent = parent;
            item._depth = depth;

            // badge
            if (item.badge) {
                if (item.badge_dot !== true) {
                    item.badge_dot = false;
                }
                if (!item.badge_status) {
                    item.badge_status = 'error';
                }
            }

            item.hide = item.acl && !this.aclService.can(item.acl);
            item._type = item.externalLink ? 2 : 1;
            if (item.children && item.children.length > 0) {
                item._type = 3;
            }
        });
    }

    get menus() {
        return this.data;
    }

    setDefault(url: string) {
        if (!url) {
            return;
        }

        let findItem: Menu = null;
        this.visit(item => {
            item._open = false;
            if (!item.link) {
                return;
            }
            if (!findItem && item.link.includes(url)) {
                findItem = item;
            }
        });
        if (!findItem) {
            console.warn(`not found page name: ${url}`);
            return;
        }

        do {
            findItem._open = true;
            findItem = findItem.__parent;
        } while (findItem);
    }
}



